import React from 'react'
import './CSS/Header.css'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import {Link} from 'react-router-dom'

import 'bootstrap/dist/css/bootstrap.min.css';
function Header() {
    return (
        
        <Navbar className="Headcontainer" collapseOnSelect expand="md" fixed="top">
  <Navbar.Brand style={{fontSize:"22px",fontWeight:'900',color:'#ffebcd'}}>Sanmati Enterprises</Navbar.Brand>
  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
  <Navbar.Collapse id="responsive-navbar-nav">
    <Nav className="mr-auto">
      <Nav.Link ><Link className="linkText" to='/'>Home</Link></Nav.Link>
      <Nav.Link ><Link className="linkText" to='/products'>Products</Link></Nav.Link>
      <Nav.Link ><Link className="linkText" to='/gallery'>Gallery</Link></Nav.Link>
      <Nav.Link ><Link className="linkText" to='/contactus'>Contact Us</Link></Nav.Link>
      
    </Nav>
  </Navbar.Collapse>
</Navbar>
    )
}

export default Header
