import React from 'react'
import Carousel from 'react-bootstrap/Carousel'
import first from '../New folder/Gallery/first.JPG'
import second from '../New folder/Gallery/second.JPG'
import third from '../New folder/Gallery/third.jpg'

export default function CarouselCard() {
    return (
        <div className="carContainer">
            <Carousel>
  <Carousel.Item>
    <img
      className="d-block w-100"
      src={first}
      alt="First slide"
    />
    <Carousel.Caption>
    </Carousel.Caption>
  </Carousel.Item>
  <Carousel.Item>
    <img
      className="d-block w-100"
      src={second}
      alt="Third slide"
    />

    <Carousel.Caption>
     
    </Carousel.Caption>
  </Carousel.Item>
  <Carousel.Item>
    <img
      className="d-block w-100"
      src={third}
      alt="Third slide"
    />

    <Carousel.Caption>
      
    </Carousel.Caption>
  </Carousel.Item>
</Carousel>
        </div>
    )
}
