import React from 'react'
import Card from './Card'
function Products() {

    var products=["Automobile Enginners", "Boilers", "Brakes","Clutch","DOM","Extra","Fluid Machinery","Fluid Mechanics","HMT","Machine Drawing","Measurement","Pneumetic","RAC","Refrigeration","Structures","Wind Tunnel"]
    var product = products.map(item => <Card name={item} />)
    return (
        <div id="bigContainer">
            {product}
        </div>
    )
}

export default Products
