import React from 'react'
import './CSS/Card.css'
import bg from '../New folder/home.jpg'
function Contact({name}) {
    return (
        <div><a href="https://drive.google.com/drive/folders/1z4FKn3nvLa9HvQE0Uj0ucpzEoHLRispc" target="_blank">
            <div className="Cardcontainer" >
                <img src={bg}></img>
                <div className="cardText">{name}</div>

            </div></a>

        </div>
    )
}

export default Contact
